button {
    border-radius: 6px;
    border: none;
    background-color: var(--primary);
    padding-inline: 1rem;
    padding-block: 10px;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    cursor: pointer;
}

button:disabled {
    background-color: var(--grey);
    cursor: not-allowed;
}