@import url(https://fonts.googleapis.com/css2?family=Rye&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&family=Rye&display=swap);
:root {
  --primary: #B45309;
  --secondary: #FFFBEB;
  --grey: #dddddd;
}

* {
  margin: 0;
  font-family: 'Open Sans';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  display: flex;
  justify-content: center;
  background-color: #dddddd;
  background-color: var(--grey);
}
header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-block: 1rem;
    padding-inline: 1.5rem;
    font-family: Rye;
    font-size: 1.5rem;
    text-transform: uppercase;
    font-weight: 400;
    color: var(--primary);
    background-color: var(--secondary);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
}

header a img {
    width: 16px;
    height: 14px;
    margin: 0;
}
header a {
    display: flex;
    justify-content: center;
    align-items: center;
}
.input {
    font-size: 14px;
    border-radius: 6px;
    border: 1px solid var(--grey);
    padding-inline: 1rem;
    padding-block: .75rem;
    width: 100%;
}
button {
    border-radius: 6px;
    border: none;
    background-color: var(--primary);
    padding-inline: 1rem;
    padding-block: 10px;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    cursor: pointer;
}

button:disabled {
    background-color: var(--grey);
    cursor: not-allowed;
}
img {
    width: 60px;
    height: 60px;
}
p {
    font-style: italic;
    font-weight: 700;
    text-align: center;
}
.container {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  padding-inline: 1rem;
  padding-block: 1.5rem;
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: white;
}

@media (min-width: 768px) {
  .wrapper {
    width: 50%;
  }
}
