header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-block: 1rem;
    padding-inline: 1.5rem;
    font-family: Rye;
    font-size: 1.5rem;
    text-transform: uppercase;
    font-weight: 400;
    color: var(--primary);
    background-color: var(--secondary);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
}

header a img {
    width: 16px;
    height: 14px;
    margin: 0;
}
header a {
    display: flex;
    justify-content: center;
    align-items: center;
}